import * as d3 from "d3";

import data from "./day.csv";
function test2() {

    // SVG
    var width = 1000;
    var height = 500;
    var margin = { top: 40, right: 50, bottom: 60, left: 50 };
    var innerWidth = width - margin.left - margin.right;
    var innerHeight = height - margin.top - margin.bottom;

    // divSVG
    var container = d3.select("body")
        .append("div")
        .attr("class", "chart-container");

    // 
    container.append("h2")
        .text("10");

    // SVG
    var svg = container.append("svg")
        .attr("width", width)
        .attr("height", height);

    // SVG
    var g = svg.append("g")
        .attr("transform", "translate(" + margin.left + "," + margin.top + ")");

    // X
    var xScale = d3.scaleBand()
        .domain(data.map(d => d.day))
        .range([0, innerWidth])
        .padding(0.1);

    // Y
    var yScale = d3.scaleLinear()
        .domain([0, d3.max(data, d => Math.max(d.high, d.low))])
        .range([innerHeight, 0]);

    // XYSVG
    var xAxis = d3.axisBottom(xScale);
    var yAxis = d3.axisLeft(yScale);

    // SVGX
    g.append("g")
        .attr("class", "x-axis")
        .attr("transform", "translate(0," + innerHeight + ")")
        .call(xAxis);
    // SVGY
    g.append("g")
        .attr("class", "y-axis")
        .call(yAxis);

    // 
    var line1 = d3.line()
        .x(d => xScale(d.day) + xScale.bandwidth() / 2)
        .y(d => yScale(d.high))
        .curve(d3.curveBumpX);
    // 
    var line2 = d3.line()
        .x(d => xScale(d.day) + xScale.bandwidth() / 2)
        .y(d => yScale(d.low))
        .curve(d3.curveBumpX);

    // SVG
    g.append("path")
        .datum(data)
        .attr("fill", "none")
        .attr("stroke", "red")
        .attr("stroke-width", 2.5)
        .attr("d", line1)
        .style("stroke-dasharray", ("3, 3")) // 
        .style("stroke-linejoin", "round") // 
        .style("stroke-linecap", "round") // 
        .transition()
        .duration(1000)
        .attrTween("stroke-dasharray", function () {
            var length = this.getTotalLength();
            return function (t) {
                return d3.interpolateString("0," + length, length + "," + length)(t);
            };
        });

    // SVG
    g.append("path")
        .datum(data)
        .attr("fill", "none")
        .attr("stroke", "blue")
        .attr("stroke-width", 2.5)
        .attr("d", line2)
        .style("stroke-dasharray", ("3, 3")) // 
        .style("stroke-linejoin", "round") // 
        .style("stroke-linecap", "round") // 
        .transition()
        .duration(1000)
        .attrTween("stroke-dasharray", function () {
            var length = this.getTotalLength();
            return function (t) {
                return d3.interpolateString("0," + length, length + "," + length)(t);
            };
        });

    // 
    g.selectAll(".data-point-high")
        .data(data)
        .enter()
        .append("circle")
        .attr("class", "data-point-high")
        .attr("cx", d => xScale(d.day) + xScale.bandwidth() / 2)
        .attr("cy", d => yScale(d.high))
        .attr("r", 0)
        .style("fill", "red") // 
        .on("mouseover", function () {
            d3.select(this)
                .transition()
                .attr("r", 6);
        })
        .on("mouseout", function () {
            d3.select(this)
                .transition()
                .attr("r", 4);
        })
        .transition()
        .delay(1000)
        .duration(500)
        .attr("r", 4);

    // 
    g.selectAll(".data-point-low")
        .data(data)
        .enter()
        .append("circle")
        .attr("class", "data-point-low")
        .attr("cx", d => xScale(d.day) + xScale.bandwidth() / 2)
        .attr("cy", d => yScale(d.low))
        .attr("r", 0)
        .style("fill", "blue") // 
        .on("mouseover", function () {
            d3.select(this)
                .transition()
                .attr("r", 6);
        })
        .on("mouseout", function () {
            d3.select(this)
                .transition()
                .attr("r", 4);
        })
        .transition()
        .delay(1000)
        .duration(500)
        .attr("r", 4);

    // 
    g.selectAll(".data-label-high")
        .data(data)
        .enter()
        .append("text")
        .attr("class", "data-label-high")
        .attr("x", d => xScale(d.day) + xScale.bandwidth() / 2)
        .attr("y", d => yScale(d.high) - 10)
        .text(d => d.high)
        .style("text-anchor", "middle")
        .style("opacity", 0)
        .transition()
        .delay(1000)
        .duration(500)
        .style("opacity", 1);

    // 
    g.selectAll(".data-label-low")
        .data(data)
        .enter()
        .append("text")
        .attr("class", "data-label-low")
        .attr("x", d => xScale(d.day) + xScale.bandwidth() / 2)
        .attr("y", d => yScale(d.low) + 15)
        .text(d => d.low)
        .style("text-anchor", "middle")
        .style("opacity", 0)
        .transition()
        .delay(1000)
        .duration(500)
        .style("opacity", 1);

    // 
    g.selectAll(".data-point-high, .data-point-low")
        .on("mouseover", function () {
            d3.select(this)
                .transition()
                .attr("r", 6);
        })
        .on("mouseout", function () {
            d3.select(this)
                .transition()
                .attr("r", 4);
        });

}

export default test2;
test2();